import React from 'react'
import { Link } from 'gatsby'
import 'animate.css/animate.min.css'
import ScrollAnimation from 'react-animate-on-scroll'
import styled, { css } from 'styled-components'
import { color } from '@style/color'
import { globalType } from '@style/typography'
import { screen } from '@style/screen'

class Navi extends React.Component {
  render() {
    const { location, title } = this.props
    return (
      <Nav>
        <ScrollAnimation
          animateIn="fadeIn"
          animateOut="fadeOut"
          offset={0}
          duration={0.3}
        >
          <Link to="/">
            <Brand>{title}</Brand>
          </Link>
        </ScrollAnimation>
        <ScrollAnimation
          animateIn="fadeIn"
          animateOut="fadeOut"
          offset={0}
          duration={0.3}
        >
          {location.pathname === '/about/' ? (
            <Link to="/readme/">
              <NavLink focus>Readme</NavLink>
            </Link>
          ):(
            <Link to="/readme/">
              <NavLink normal>Readme</NavLink>
            </Link>
          )}
        </ScrollAnimation>
      </Nav>
    )
  }
}

export default Navi

const Nav = styled.div`
  position: fixed;
  ${globalType.font.primary};
  width: 100%;
  padding: 5px 0;
  a {
    text-decoration: none !important;
    color: ${color.black};
    :hover{
      color: ${color.grayscale.S900};
    }
  }
  @media (max-width: ${screen.md}) {
    display: grid;
    grid-template-columns: repeat(auto-fill, 100px);
  }
`
const Brand = styled.div`
  padding: 10px 20px;
  font-weight: bold;
  ${globalType.size.lg};
  @media (max-width: ${screen.md}) {
    padding: 10px 15px;
  }
`
const NavLink = styled.div`
  text-decoration: none;
  float: left;
  display: block;
  padding: 0px 20px;
  font-weight: bold;
  width: 100%;
  ${globalType.size.sm};
  color: ${color.grayscale.S950};
  @media (max-width: ${screen.md}) {
    padding: 14px 15px;
    display: inline-block;
  }
  ${props =>
    props.normal &&
    css`
      :before {
        content: '';
        padding-left: 0;
        overflow: hidden;
        transition: 0.2s padding;
      }
      :hover{
        color: ${color.grayscale.S950};
        :before{
          padding: 0 3px;
        }
      }
    `
  }
  ${props =>
    props.focus &&
    css`
      color: ${color.primary.S400};
      :before {
        content: '⁃';
        margin-right: 5px;
      }
    `
  }
`