import styled from 'styled-components'
import { color } from '@style/color'
import { screen } from '@style/screen'
import { globalType, paragraph } from '@style/typography'

export const Container = styled.div`
  position: relative;
  top: 0;
  left: 0;
  z-index: 10;
  text-rendering: optimizeLegibility !important;
  -webkit-font-smoothing: antialiased;
  width: 100%;
  margin: 0 auto;
  padding: 0 16px;
  background-color: ${props => props.color};
  p{
    ${globalType.font.base};
    ${globalType.size.base};
    ${paragraph.style};
  }
  ${props =>
    props.fluid && `
    max-width: 3500px !important;
  `};
  ${props =>
    props.divider && `
    border-top: 1px solid ${color.grayscale.S400};
    border-bottom: 1px solid ${color.grayscale.S400};
  `};
  @media (min-width: ${screen.xs}) {
    max-width: 540px;
  }
  @media (min-width: ${screen.sm}) {
    max-width: 720px;
  }
  @media (min-width: ${screen.md}) {
    max-width: 960px;
  }
  @media (min-width: ${screen.lg}) {
    max-width: 1200px;
  }
  @media (min-width: ${screen.xl}) {
    max-width: 2000px;
  }
  @media (min-width: ${screen.xxl}) {
    max-width: 2500px;
  }
`
export const Section = styled.section`
  padding: 30px 0;
  padding: ${props => props.padding};
  overflow: hidden;
  ${props =>
    props.borderTop && `
    border-top: 1px solid ${color.grayscale.S500}; 
  `};
`
export const Box = styled.div`
  width: ${props => props.width};
  margin: ${props => props.margin};
  padding: ${props => props.padding};
  background-color: ${props => props.color};
  ${props =>
    props.borderTop && `
    border-top: 1px solid ${color.grayscale.S500}; 
  `};
  ${props =>
    props.p && `
      padding: 15px;
      @media (max-width: ${screen.lg}) {       
        padding: 15px;
      }
  `};
  @media (max-width: ${screen.lg}) {       
    width: 100% !important;
    padding: 15px;
  }
`
export const GridCol = styled.div`
  display: grid;
  grid-template-columns: ${props => props.col.desktop};
  grid-gap: 40px;
  grid-gap: ${props => props.gap};
  margin: ${props => props.margin};
  padding: ${props => props.padding};
  @media (max-width: ${screen.lg}) {
    grid-template-columns: 1fr;
    grid-gap: 10px;
    grid-template-columns: ${props => props.col.mobile};
  }
`
export const FlexRow = styled.div`
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  @media (max-width: ${screen.lg}) {
    flex-direction: column;
  }
`